'use client'

import React, { useEffect, useState } from "react";
import {
  Row,
  Col,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Button,
} from "reactstrap";
import "react-datepicker/dist/react-datepicker.css";
import { toast } from "react-hot-toast";
import { useRouter } from "next/navigation";
import { useTranslation } from "react-i18next";
import { useStateMachine } from "little-state-machine";
import {
  updateBooking,
  updateAction,
  updateUser,
} from "../../utils/updateActions";
import ShortUniqueId from "short-unique-id";
import { useForm } from "react-hook-form";
import axios from "axios";
import { SpinnerCircular } from 'spinners-react';
import type { Trip } from "../../interfaces/trips.interface";
import type { Booking } from "../../interfaces/booking.interface";

interface UserBookingModalProps {
  modal: { modal_static: boolean };
  getModal: (value: { modal_static: boolean }) => void;
  booking?: Trip;
  buttonText?: string;
}

interface BookingFormData {
  name: string;
  idCardNo: string;
  phoneNumber: string;
  payerEmail: string;
}


const PAYUNIT_USERNAME = "4d70631d-397c-4efd-929a-87fcd3a9d162";
const PAYUNIT_PASSWORD = "2a8f415d-d9d2-4834-a573-b12c1375b94c";

const UserBookingModal: React.FC<UserBookingModalProps> = ({
  modal,
  getModal,
  booking,
  buttonText = "Pay Now",
}) => {
  const { t } = useTranslation();
  const router = useRouter();
  const {
    register,
    reset,
    getValues,
    handleSubmit,
    formState: { errors },
  } = useForm<BookingFormData>();

  const [loading, setLoading] = useState<boolean>(false);
  const { state } = useStateMachine({
    updateBooking,
    updateAction,
    updateUser,
  });

  const token = `${PAYUNIT_USERNAME}:${PAYUNIT_PASSWORD}`;
  const encodedToken = typeof Buffer !== 'undefined' ? Buffer.from(token).toString("base64") : btoa(token);
  const uid = new ShortUniqueId({ length: 10, dictionary: "alphanum_upper" });
  const transaction_id = uid();

  useEffect(() => {
    if (booking) {
      reset({
        data: booking,
      } as any);
    }
  }, [booking, reset]);

  const makePaymentOnPayunit = async (): Promise<void> => {
    setLoading(true);
    const phone = getValues("phoneNumber");
    const name = getValues('name');
    const idCard = getValues('idCardNo');
    const payerEmail = getValues('payerEmail');

    if (typeof window !== 'undefined') {
      localStorage.setItem("phoneNumber", phone);
      localStorage.setItem("name", name);
      localStorage.setItem("idCard", idCard);
      localStorage.setItem("email", payerEmail);
      localStorage.setItem("transactionID", transaction_id);
    }

    const config = {
      headers: {
        "x-api-key": process.env.NEXT_PUBLIC_PAYUNIT_API_KEY || "live_jpKGxzmP3riiu7jmal2zU2FgnjfBE67HMHf3Pb4Z",
        mode: "live",
        "Content-Type": "application/json",
        Authorization: "Basic " + encodedToken,
      },
    };

    const trip = (state?.trip as any)?.trip;
    const booked = {
      total_amount: trip?.price || 100,
      currency: "XAF",
      transaction_id: transaction_id,
      return_url: `${process.env.NEXT_PUBLIC_APP_URL || 'https://transapp-user.herokuapp.com'}/payunit-return/:transaction_id/:transaction_amount/:transaction_gateway/:transaction_status/:purchaseRef/:currency`,
    };

    try {
      const { data } = await axios.post(
        `https://app.payunit.net/api/gateway/initialize`,
        booked,
        config
      );
      if (data?.error) {
        toast.error(data.message);
        setLoading(false);
      } else {
        if (data.status === "SUCCESS" && typeof window !== 'undefined') {
          window.location.href = data.data.transaction_url;
        }
      }
    } catch (error: unknown) {
      setLoading(false);
      if (axios.isAxiosError(error)) {
        toast.error(error.response?.data?.message || 'Payment initialization failed');
      } else {
        toast.error('An unexpected error occurred');
      }
    }
  };

  const trip = (state?.trip as any)?.trip;

  return (
    <>
      <Modal isOpen={modal.modal_static} backdrop="static" centered size="lg">
        <ModalHeader toggle={() => getModal({ modal_static: false })}>
          Destination: {trip?.to_location || trip?.to}
        </ModalHeader>
        <ModalBody>
          <form onSubmit={handleSubmit(makePaymentOnPayunit)}>
            <Row>
              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="name">{t("name")}</label>
                  <input
                    type="text"
                    name="name"
                    id="name"
                    className="form-control"
                    {...register("name", {
                      required: true,
                      minLength: 6,
                    })}
                  />
                </div>
                {errors.name && (
                  <p style={{ color: "red" }}>{t("agency_branch_err")}</p>
                )}
              </Col>
              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="idCardNo">{t("id_card")}</label>
                  <input
                    type="text"
                    name="idCardNo"
                    id="idCardNo"
                    className="form-control"
                    {...register("idCardNo", {
                      required: true,
                    })}
                  />
                </div>
                {errors.idCardNo && (
                  <p style={{ color: "red" }}>{t("id_card_err")}</p>
                )}
              </Col>

              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="phoneNumber">{t("phone")}</label>
                  <input
                    type="number"
                    name="phoneNumber"
                    id="phoneNumber"
                    placeholder="Valid money transfer number"
                    className="form-control"
                    {...register("phoneNumber", {
                      required: true,
                      minLength: 9,
                      maxLength: 12,
                    })}
                  />
                </div>
                {errors.phoneNumber && (
                  <p style={{ color: "red" }}>{t("phone_err")}</p>
                )}
              </Col>

              <Col lg={6}>
                <div className="form-group">
                  <label htmlFor="payerEmail">{t("email_address")}</label>
                  <input
                    type="email"
                    name="payerEmail"
                    id="payerEmail"
                    className="form-control"
                    {...register("payerEmail", {
                      required: true,
                    })}
                  />
                </div>
                {errors.payerEmail && (
                  <p style={{ color: "red" }}>{t("phone_err")}</p>
                )}
              </Col>
            </Row>

            <ModalFooter>
              {loading ? (
                <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', padding: '20px' }}>
                  <SpinnerCircular
                    color="#bcc015"
                    size={50}
                    aria-label="Loading Spinner"
                    data-testid="loader"
                  />
                </div>
              ) : (
                <>
                  <Button
                    color="light"
                    onClick={() => getModal({ modal_static: false })}
                  >
                    {t("cancel")}
                  </Button>
                  <Button type="submit" color="primary">
                    {buttonText}
                  </Button>
                </>
              )}
            </ModalFooter>
          </form>
        </ModalBody>
      </Modal>
    </>
  );
};

export default UserBookingModal;
